<template>
    <div>
      <!--人员信息表格头部 工具组件-->
      <!--<per-info-table-tool-bar :page="page" :searchConfigs="searchConfigs" @tabEvent="tabEvent"></per-info-table-tool-bar>-->
      <!--人员信息表格组件-->
      <per-main-table ref="perMainTable" :searchConfigs="searchConfigs" :siteContentViewHeight="siteContentViewHeight" :page="page" @updatePage="updatePage" @updateSelect="updateSelect" :selectedData="selectedData"></per-main-table>
      <!--分页组件-->
      <!--<pagination ref="pagination" :page="page" @pageChange="pageChange" @pageChangeSize="pageChangeSize" @viewChange="viewChange" selectIsShow="selectIsShow"></pagination>-->
    </div>
</template>

<script>
    import pagination from '@/components/baseComponents/newPagination'
    import perInfoTableToolBar from '../perInfoTableToolBar/perInfoTableToolBar'
    import perMainTable from './perMainTable'
    export default {
        name: "perInfoTable",
        props:['siteContentViewHeight','searchConfigs'],
        components:{
            pagination,perInfoTableToolBar,perMainTable
        },
        data() {
            return {
                page: {
                    pageIndex:this.API.constObj.newpageIndex,//当前页码
                    pageSize:this.API.constObj.newpageSize,//每页大小
                    total: 0//总条数
                },
                selectIsShow:true,
                selectedData:{},
                mainTableProps:{
                  tabEventName:'',
                }
            }
        },
        created(){
          console.log(this.API.constObj)
            console.log(this.page)
        },
        methods: {
            //分页组件回调方法
            PageTurning(page_obj) {
                console.log(page_obj)
                this.page.pageIndex=page_obj.page_index;
                this.page.pageSize=page_obj.page_size;
                this.$refs.perMainTable.getDataList()
            },
            updateSelect(val){
              this.selectedData = val
            },
            updatePage(pageObj){
              console.log(pageObj)
                this.page.total = pageObj.total
                // this.$refs.pagination.initPage(pageObj)
            },

            pageChange(val){
                //分页
                this.page.pageIndex=val
                this.$refs.perMainTable.getDataList()
            },
            pageChangeSize(val){
                //分页
                this.page.pageSize=val
                this.$refs.perMainTable.getDataList()
            },
            getTableData(){
              this.$refs.perMainTable.getDataList()
            },
            tabEvent(val){
              this.mainTableProps.tabEventName = val
              this.$refs.perMainTable.matchEvent()
              console.log(val)
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>



