<template>
  <el-dialog
    :title="title"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="30%">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="100px">
      <el-form-item label="所属类型" prop="nameListType">
        <el-select v-model="dataForm.nameListType" :disabled='disabled' style="width: 100%;">
          <el-option label="共享名单" value="1"></el-option>
          <el-option label="自定义名单" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名单名称" prop="name">
        <el-input v-model="dataForm.name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        dataForm: {nameListType:'',name:'',id:''},
        dataRule: {
          nameListType: [
            { required: true, message: '请选择所属类型', trigger: 'change'}
          ],
          name: [
            { required: true, message: '名单名称必填', trigger: 'blur' }
          ]
        },
        disabled:false,
        title:'',
        operateType:''
      }
    },
    methods: {
      init(row,operateType){
        this.title = operateType == 'add' ? '新增名单' : '修改名单'
        this.operateType = operateType
        console.log(row)
        console.log(operateType)
        this.dataForm.id=row && row.listId || 0
        this.visible=true
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(operateType == 'edit') {
            this.dataForm.nameListType=row.listType
            this.dataForm.name=row.listName
            this.disabled=true
          }else this.disabled=false
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.dataForm.id=this.dataForm.id || undefined

            let params = {
              listType:this.dataForm.nameListType,
              listName:this.dataForm.name
            }
            if(this.operateType == 'add'){
              params.id = this.dataForm.id
            }else{
              params.listId = this.dataForm.id
            }

            this.$http({
              url: this.$http.adornUrl(this.API[this.operateType == 'add'?'saveNameList':'updateNameList']),
              method:'post',
              data:this.$http.adornData(params)
            }).then(({data}) => {
              if(data){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshNameList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            })
          }
        })
      }
    }
  }
</script>

<style>
</style>



