<template>
  <!-- 干部信息管理 -->
  <el-container class="permission-assignment">
    <el-aside width='22%' class="permission-assignment-aside">
      <!--<el-row type="flex">-->
        <!--<el-col :span="12">-->
          <!--<el-select v-model="stateValue" size="small">-->
          	<!--<el-option v-for="item in stateData" :key="item.itemCode" :label="item.itemName" :value="item.itemCode"> </el-option>-->
          <!--</el-select>-->
        <!--</el-col>-->
        <!--<el-col :span="12">-->
          <!--<el-select v-model="typeValue" size="small">-->
          	<!--<el-option v-for="item in typeData" :key="item.itemCode" :label="item.itemName" :value="item.itemCode"> </el-option>-->
          <!--</el-select>-->
        <!--</el-col>-->
      <!--</el-row>-->
      <el-radio-group v-model="activeName" @change="change">
        <el-radio-button label="first">机构</el-radio-button>
        <el-radio-button label="second">名单</el-radio-button>
      </el-radio-group>
      <div v-if="activeName=='first'">
        <!--<el-input placeholder='姓名查询(多人查询用","分开)'  size="small"  v-model="namesValue"  @input="searchName"></el-input>-->
<!--        <el-scrollbar :style="{'height':siteContentViewHeight - 210 + 'px' }">-->
<!--          <org-tree ref="tree" :treeData="treeData"-->
<!--            :checkbox='multiAgencyQuery'-->
<!--            :filterText='filterText'-->
<!--            :checkStrictly='!includeSubordinate'-->
<!--            @node-click="orgTreeClick"></org-tree>-->
<!--        </el-scrollbar>-->
        <div :style="{'height':siteContentViewHeight - 122 + 'px' ,overflow:'auto'}">
          <org-tree ref="tree" :treeData="treeData"
                    :checkbox='multiAgencyQuery'
                    :filterText='filterText'
                    :checkStrictly='!includeSubordinate'
                    @node-click="orgTreeClick" @orgTreeClick2="orgTreeClick2" @orgTreeHandle="orgTreeHandle" :multiAgencyQuery="searchConfigs.multiAgencyQuery"></org-tree>
        </div>
        <div>
          <el-checkbox v-model="multiAgencyQuery" @change="multi">多机构查询</el-checkbox>
          <el-checkbox v-model="includeSubordinate">包含下级</el-checkbox>
        </div>
        <el-input class="aside-search" placeholder="机构查询" v-model="filterText" size="medium"></el-input>
        <!--<el-button type="primary" style="width: 100%;" @click="searchHandle">开始查询</el-button>-->
      </div>
      <div v-else>
        <el-form :inline="true">
          <el-form-item>
            <el-button type="primary" size="mini" @click="addOrUpdateNamelistHandle('add')">新建</el-button>
            <el-button type="primary" size="mini" @click="deletNamelisteHandle(nameListSelect || 0)">删除</el-button>
            <el-button type="primary" size="mini" @click="addOrUpdateNamelistHandle('edit')">更名</el-button>
            <el-button type="primary" size="mini" @click="shareNamelistHandle(nameListSelect || 0)">分享</el-button>
          </el-form-item>
        </el-form>
        <org-tree ref="tree" :treeData="nameListData" :defaultProps="defaultProps" @node-click="nameListTreeClick"></org-tree>
      </div>
    </el-aside>
    <el-main class="rightContent">
      <div class="searchBox">
        <div class="searchInput">
          <el-form :inline="true" :model="conditionName" class="demo-form-inline">
            <!--<el-form-item label="机构">-->
              <!--<el-select v-model="conditionName.region" placeholder="活动区域" size="small">-->
                <!--<el-option label="区域一" value="shanghai"></el-option>-->
                <!--<el-option label="区域二" value="beijing"></el-option>-->
              <!--</el-select>-->
            <!--</el-form-item>-->
            <el-form-item label="姓名">
              <el-input v-model="conditionName.name" placeholder="姓名"  size="small" :clearable="true"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"  size="small" @click="searchBtn">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" plain :icon="iconPlus"  size="small" @click="openSearchOptions"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"  size="small" @click="resetCheckList">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!--<el-collapse-transition>-->
          <div class="searchContent" v-if="searchOptionsShow">
            <el-form :inline="true"  class="demo-form-inline">

                <el-form-item  v-for="(item,index) in conditionCheckbox" :label="item.name" :key="item.id">

                  <el-checkbox-group
                    v-model="item.conditionCheckList"
                  >

                  <el-checkbox v-for="item1 in item.child" :label="item1.id" :key="item.id">{{item1.name}}</el-checkbox>
                  <span v-if="item.divider" class="el-divider"></span>

                  </el-checkbox-group>
                </el-form-item>




            </el-form>
          </div>
        <!--</el-collapse-transition>-->

      </div>
      <!--人员信息表格头部工具栏-->
<!--      <per-info-tableTool-bar></per-info-tableTool-bar>-->
      <!--人员信息表格-->
      <!--<perInfoTable ref="perInfoTable" class="main-table" :searchConfigs="searchConfigs" :siteContentViewHeight="siteContentViewHeight"></perInfoTable>-->
      <perMainTable :page='page' ref="perMainTable" class="main-table" :searchConfigs="searchConfigs" :siteContentViewHeight="siteContentViewHeight" :siteContentViewHeightFix="siteContentViewHeightFix" :conditionCheckbox="conditionCheckbox" :conditionName="conditionName"></perMainTable>
      <!-- 弹窗，名单新增、修改 -->
      <add-or-update-namelist v-if="addOrUpdateNamelistVisible" ref='addOrUpdateNamelist' @refreshNameList="getNameList"></add-or-update-namelist>
      <!-- 弹窗，分享 -->
      <share-namelist v-if="shareNamelistVisible" ref='shareNamelist'></share-namelist>
    </el-main>
  </el-container>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  import addOrUpdateNamelist from './add-or-update-namelist.vue'
  import shareNamelist from './share-namelist.vue'
  import perInfoTableToolBar from './perInfoTableToolBar/perInfoTableToolBar'
  import perMainTable from './perInfoTable/perMainTable'
  import perInfoTable from './perInfoTable/perInfoTable'
  export default{
    props:['siteContentViewHeight','siteContentViewHeightFix'],
    components:{
        orgTree,addOrUpdateNamelist,shareNamelist,perInfoTable,perInfoTableToolBar,perMainTable
    },
    data(){
      return{
        stateValue:'',
        iconPlus:'el-icon-plus',
        stateData:[
          {itemCode:'',itemName:'全部'},
          {itemCode:'1',itemName:'现职人员'}
        ],
        typeValue:'',
        typeData:[
          {itemCode:'',itemName:'全部'},
          {itemCode:'1',itemName:'现职人员'}
        ],
        activeName:'first',
        treeData:[],
        multiAgencyQuery:false,
        includeSubordinate:false,
        filterText: '',
        nameListData:this.newNameListData(),
        nameListSelect:'',
        addOrUpdateNamelistVisible:false,
        shareNamelistVisible:false,
        searchConfigs:{
          multiAgencyQuery:false,
          orgId:[],
          currentOrgItem:{},
          searchAreaHeight:58
        },
        conditionName: {
          name: ''
        },
        conditionCheckList:['3','8','10'],
        orignconditionCheckList:[],
        checkList: ['选中且禁用','复选框 A'],
        searchOptionsShow:false,
        conditionCheckbox:[
          {
            name:'去重',
            itemname:'qc',
            child:[
              {name:'',id:'1'}
            ],
            divider:true,
            conditionCheckList:[]
          },
          {
            name:'名册状态',
            itemname:'a02.A0279',
            child:[
              {name:'上名册',id:'2'},
              {name:'不上名册',id:'3'},
            ],
            divider:true,
            conditionCheckList:[]
          },
          {
            name:'在职状态',
            itemname:'zzzt',
            child:[
              {name:'在任',id:'1'},
              {name:'已免',id:'0'},
            ],
            divider:false,
            conditionCheckList:[]
          },
          {
            name:'管理状态',
            itemname:'a01.A0163',
            child:[
              {name:'现职人员',id:'1'},
              {name:'离退人员',id:'8'},
              {name:'调出人员',id:'9'},
              {name:'已去世',id:'10'},
              {name:'其他人员',id:'11'},
              {name:'免职人员',id:'12'}
            ],
            divider:false,
            conditionCheckList:[]
          },
          {
            name:'管理类别',
            itemname:'a01.A0165',
            child:[
              {name:'中管',id:'01'},
              {name:'省管',id:'02'},
              {name:'市管',id:'03'},
              {name:'县管',id:'04'},
              {name:'其他',id:'09'}
            ],
            divider:false,
            conditionCheckList:[]
          }
        ],
        orignconditionCheckbox:[],
        defaultProps: {
          children: 'children',
          label: 'listName'
        },
        page: {
          pageIndex:this.API.constObj.newpageIndex,//当前页码
          pageSize:this.API.constObj.newpageSize,//每页大小
          total: 0//总条数
        },
        namesValue:'',
        operateType:''
      }
    },
    created(){
      console.log(this.siteContentViewHeight)
      this.getDataTree();
      this.getNameList();
      // this.orignconditionCheckList =  Array.from(this.conditionCheckList)

      this.orignconditionCheckbox = JSON.parse(JSON.stringify(this.conditionCheckbox))
      console.log(this.orignconditionCheckbox)
    },
    methods:{
      searchName(val){
        console.log(val)
      },
      newNameListData(){
        return [
          {"id":"1","listName":"共享名单","children":[]},
          {"id":"2","listName":"自定义名单","children":[]},
          {"id":"3","listName":"分享名单","children":[]}
        ]
      },
      getDataTree(){//获取机构
        this.$http({
          url: this.$http.adornUrl(this.API.getOrganizationTree),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.treeData=data.data;
            // this.orgTreeClick(this.treeData[0])
          }else{
            this.$message.error(data.msg);
            this.treeData=this.newNameListData();
          }
        });
      },
      //机构树点击事件
      orgTreeClick(data) {
        // console.log(data)

      },
      orgTreeClick2(data){
        console.log(data)
        this.$refs.perMainTable.scroll()
        //机构树点击
        this.searchConfigs.orgId = data
        if(this.searchOptionsShow){
          this.searchConfigs.searchAreaHeight = 204
        }else{
          this.searchConfigs.searchAreaHeight = 58
        }

      },
      orgTreeHandle(val){
        console.log(val)
        this.searchConfigs.nameListSelect = undefined
        this.searchConfigs.currentOrgItem = val
        this.$refs.perMainTable.updatDataList()
      },
      getNameList(){
        this.$http({
          url: this.$http.adornUrl(this.API.getNameList),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.nameListData[0].children=data.data['1'];
            this.nameListData[1].children=data.data['2']
            this.nameListData[2].children=data.data['3']
          }else{
            this.$message.error(data.msg);
            this.nameListData=[];
          }
        });
      },
      nameListTreeClick(data){
        console.log(data)
        this.searchConfigs.nameListSelect = data
        this.nameListSelect=data;
        this.$refs.perMainTable.updatDataList()
      },
      addOrUpdateNamelistHandle(data){//名单新增、修改
        if(data  == 'add'){
          this.addOrUpdateNamelistVisible=true;
          this.$nextTick(() => {
            this.$refs.addOrUpdateNamelist.init(this.nameListSelect,data)
          })
        }else{
          if(this.nameListSelect == ''){
            this.$message.warning('请选择名单');return;
          }else if(this.nameListSelect.id){
            this.$message.warning('名单类型不能修改');return;
          }else if(this.nameListSelect.shareType == '0'){
            this.$message.warning('分享名单不能修改');return;
          }else{
            this.addOrUpdateNamelistVisible=true;
            this.$nextTick(() => {
              this.$refs.addOrUpdateNamelist.init(this.nameListSelect,data)
            })
          }
        }
        // if(data==0){
        //   this.$message.warning('请选择名单');return;
        // }
        // else if(data.id =='3'){
        //   this.$message.warning('不能修改名单类型');
        //   return;
        // }
        // else if(data.id =='1' || data.id =='2'){
        //   this.addOrUpdateNamelistVisible=true;
        // }
        // else if(data.listType == undefined){
        //   this.$message.warning('分享名单不允许修改');
        //   return;
        // }
        // this.addOrUpdateNamelistVisible=true;

      },
      deletNamelisteHandle(data){
        console.log(this.nameListSelect.listId)

        if(data == ''){
          this.$message.warning('请选择名单');return;
        }else if(data.id){
          this.$message.warning('不能删除名单类型');return;
        }
        this.$confirm('确定要删除该信息？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API[this.nameListSelect.listType==undefined?'deleteShareNameList':'deleteNameList']),
            method: 'post',
            data: this.$http.adornData({
              id:this.nameListSelect.listId
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.$message.success('操作成功');
              this.getNameList()
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      },
      shareNamelistHandle(data){//分享弹窗
        if(data==0){
          this.$message.warning('请选择名单');return;
        }else if(data.listType == '2' || data.id == '2'){
          this.shareNamelistVisible=true;
          this.$nextTick(() => {
            this.$refs.shareNamelist.init(this.nameListSelect,this.operateType)
          })
        }else{
          this.$message.warning('只允许分享自定义名单');return;
        }
      },
      change(val){
        this.nameListSelect = ''
      },
      multi(val) {
        if(!val){
            this.$refs.tree.resetChecked()
        }
        //多机构查询
        this.searchConfigs.multiAgencyQuery = val
      },
      searchHandle(){
        //开始查询
        this.$refs.perMainTable.getDataList()
        if(this.searchConfigs.multiAgencyQuery){
          this.searchConfigs.currentOrgItem = {name:''}
        }
      },
      searchBtn(){
        console.log(this.conditionCheckbox)
        this.$refs.perMainTable.getDataList()
        if(this.searchConfigs.multiAgencyQuery){
          this.searchConfigs.currentOrgItem = {name:''}
        }
      },
      openSearchOptions(){
        //打开搜索条件
        this.searchOptionsShow = !this.searchOptionsShow
        if(this.searchOptionsShow){
          this.searchConfigs.searchAreaHeight = 204
          this.iconPlus = 'el-icon-minus'
        }else{
          this.searchConfigs.searchAreaHeight = 58
          this.iconPlus = 'el-icon-plus'
        }
      },
      resetCheckList(){
        //重置查询条件
         this.conditionCheckbox = JSON.parse(JSON.stringify(this.orignconditionCheckbox))
         this.conditionName.name = ''
        // this.conditionCheckList = this.orignconditionCheckList
        // let This = this
        // this.orignconditionCheckList.forEach(function(item,index){
        //   This.$set(This.conditionCheckList, index, item)
        // })
      }

    }
  }
</script>

<style lang="scss" scoped type="text/stylus">
  .permission-assignment{
    &-aside{
      padding: 0 10px;
      .el-row{
        :first-child .el-select{padding-right:4px;}
        :last-child .el-select{padding-left:4px;}
        .el-select{padding:0 0 10px;}
      }
      .el-radio-group{
        width: 100%;
        padding-bottom: 10px;
        .el-radio-button{
          width: 50%;
          /deep/ .el-radio-button__inner{width: 100%;}
        }
      }
      .el-scrollbar{padding: 8px 0;}
      /*.aside-search{margin: 8px 0;}*/
      .aside-search{margin-top: 4px;margin-bottom: 5px;}
    }
    .el-main{overflow:hidden; padding-bottom: 0;padding-top: 0;.el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}}
    .el-aside{    box-sizing: border-box;padding-top: 10px;border-right: 1px solid #dcdfe6;.el-form--inline .el-form-item{margin-bottom: 10px;}}
    >.el-main >.el-table--border{border-left: none;}
  }

  /*.rightContent{*/
  /*  position: relative;*/
  /*}*/
  /*.main-table{*/
  /*  position: absolute;*/
  /*  top: 58px;*/
  /*  bottom: 0;*/
  /*  left: 0;*/
  /*  right: 0;*/
  /*}*/

  /*查询*/
  .searchBox{
    border-bottom: 1px solid #dcdfe6;
  }
  .searchBox .searchInput,.searchBox .searchContent{
    margin-left: 14px;
  }
 .el-main  .searchBox .searchContent   >>> .el-form--inline .el-form-item{
    margin-bottom: 4px;
    margin-top: 4px;
  }

  .searchBox >>> .el-form-item--medium .el-form-item__label{
    font-weight:bold!important;
  }
  >>>.el-divider{
    display: inline-block;
    width: 1px;
    height: 1em;
    margin: 0 8px;
    vertical-align: middle;
    position: relative;
    background-color: #dcdfe6;
    margin-left: 20px;
  }
  >>>.el-table--border, .el-table--group{
    border:none;
  }
</style>



